.review {
  background-color: $white;
  padding-bottom: 114px; }

.review__title {
  padding-top: 66px;
  font-size: 40px;
  line-height: 52px;
  text-align: center; }

.review__slider {
  position: relative;
  overflow: hidden; }


.review__slider-control {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: space-between;
  width: 1110px;
  height: 100px;
  margin: 0 auto;
  z-index: 99; }

.slider-control__button {
  position: relative;
  width: 100px;
  height: 100px;
  background-color: #f7f7f4;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.5s;
  outline: none;

  &:hover {
    background-color: #efefef; }

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 30px;
    height: 60px;
    background: no-repeat center; }

  &--left {
    &::before {
      background-image: url('../img/icon/icon-bnt-left.svg'); } }

  &--right {
    &::before {
      background-image: url('../img/icon/icon-bnt-right.svg'); } } }

.review__slider-list {
  display: flex;
  justify-content: center;
  margin-top: 40px;
  transition: 0.2s; }

.review-content {
  display: flex;
  justify-content: space-between;
  width: 768px;
  margin: 0 15px;
  border: 1px solid #E6E6E6;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.05);
  border-radius: 3px; }

.review-content__user-data {
  width: 465px;
  padding: 47px 0 0 18px; }

.review-content__user-pic {
  width: 288px; }

.review-content__user-name {
  margin: 0;
  padding-left: 25px;
  font-size: 24px;
  font-weight: 600;
  line-height: 20px; }

.review-content__user-company {
  display: block;
  margin-top: 12px;
  font-size: 18px;
  font-weight: 400;
  line-height: 20px;
  color: #828282; }

.review-content__user-feedback {
  margin-top: 30px; }

.user-feedback__title {
  position: relative;
  margin: 0;
  padding-left: 70px;
  font-size: 18px;
  font-weight: 600;
  line-height: 26px;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 57px;
    height: 45px;
    background: url('../img/icon/icon-blockquote.svg') no-repeat center; } }

.user-feedback__text {
  margin: 0;
  margin-top: 6px;
  padding-left: 70px;
  font-size: 16px;
  line-height: 26px;
  color: #000000; }

.review-content__user-pohoto {
  width: 286px;
  height: 100%;
  object-fit: cover; }
