.source-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(110, 121, 146, 0.5);
  display: none;

  &--active {
    left: 0;
    display: block; } }

.source-modal__wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  display: flex;
  flex-direction: column;
  width: 750px;
  height: 760px;
  padding: 14px 24px 45px 28px;
  background-color: $white;
  border-radius: 3px;
  overflow: hidden; }

.source-modal__title {
  margin: 0;
  font-size: 42px;
  line-height: 50px;
  font-weight: 600; }

.source-modal__button {
  position: absolute;
  top: 20px;
  right: 20px;
  margin: 0;
  padding: 0;
  width: 45px;
  height: 45px;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  transform: rotate(45deg);

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, 50%);
    width: 45px;
    height: 2px;
    background-color: $basic-gray; }

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, 50%) rotate(90deg);
    width: 45px;
    height: 2px;
    background-color: $basic-gray; } }


.source-modal__list {
  height: 100%;
  overflow-y: scroll;
  margin: 0;
  margin-top: 33px;
  font-size: 16px;
  line-height: 24px;

  &::-webkit-scrollbar {
    width: 10px; }

  &::-webkit-scrollbar-track {
     background-color: #F7F7F4 {
      border-radius: 10px; } }

  &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background-color: #474D5E; } }

.source-modal__list-item {
  width: 600px;
  margin-bottom: 5px; }

.source-modal__list-desc {
  margin: 0; }

.popup__button {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0;
    width: 45px;
    height: 45px;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    transform: rotate(45deg);
    &::before {
        content: '';
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, 50%);
        width: 63px;
        height: 2px;
        background-color: $basic-gray; }
    &::after {
        content: '';
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, 50%) rotate(90deg);
        width: 63px;
        height: 2px;
        background-color: $basic-gray; } }

.source-modal__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 535px;
  min-height: 60px;
  margin: 30px auto 0;
  font-size: 21px;
  line-height: 26px;
  color: $white;
  background-color: $basic-blue;
  border-radius: 3px;
  transition: 0.2s;

  &:hover {
    background-color: #0276D2; } }



// features modal

.feature-modal__item {
  flex-direction: column;
  width: 977px;
  height: 680px;
  padding: 26px 26px 0 26px;
  background-color: $white;
  border-radius: 3px;
  border: 1px solid #e6e6e6;
  box-shadow: 0px 5px 35px rgba(110, 121, 146, 0.5);
  display: none; }

.feature-modal__item-flex {
  .feature-modal__item {
    display: flex !important; } }

.feature-modal__title {
  font-size: 30px;
  line-height: 39px; }

.feature-modal__desc {
  margin: 0;
  margin-top: 18px;
  font-size: 20px;
  font-weight: 600;
  line-height: 28px;
  color: $basic-green; }

.feature-modal__content {
  display: flex;
  justify-content: space-between;
  margin-top: auto; }

.feature-modal__content-wrapper {
  width: 335px;
  margin-top: 35px; }

.feature-modal__content-desc {
  margin: 0;
  font-size: 16px;
  line-height: 24px;
  color: #242428; }

.feature-modal__content-list {
  margin-top: 35px; }

.feature-modal__content-item {
  position: relative;
  font-size: 16px;
  line-height: 24px;
  padding-left: 20px;

  &::before {
    content: '—';
    position: absolute;
    top: 0;
    left: 0; } }

.feature-modal__content-pic {
  width: 550px;
  height: 404px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  border: 1px solid #e6e6e6;
  border-bottom: 0;
  border-radius: 3px;

  &--center {
    height: 360px;
    align-items: center; } }

.feature-modal__button {
  margin: 0 -26px;
  padding: 23px;
  display: flex;
  background-color:#F7F7F4 {}
  border-top: 1px solid#e6e6e6; }

.feature-modal__button-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 369px;
  height: 60px;
  margin: 0 9px 0 auto;
  font-size: 21px;
  line-height: 26px;
  color: $white;
  border-radius: 3px;
  background-color: $basic-blue;
  transition: 0.2s;

  &:hover {
    background-color: #0276D2; }

  &--transparent {
    margin: 0 auto 0 9px;
    background-color: transparent;
    color: $basic-blue;
    border: 1px solid $basic-blue;

    &:hover {
      color: $white;
      background-color: rgba(0, 133, 239, 0.57);
      border: none; } } }
